@import '../../sass/variables.scss';
.main{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: $pink_theme_title;
    font-family: Kanit, sans-serif;
}

.title{
    text-align: center;
    margin-top: 16px;
}

.form{
    margin-top: 32px;
    width: 480px;
    display: flex;
    flex-direction: column;
    padding: 16px;
    background-color: white;
    color: black;
    border-radius: 24px;
}

.form > * + *{margin-top: 16px !important;}


.sliderContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.slider{color:$CTA !important}
.label{}
.button{background-color: $CTA !important;}
