.connect_button{
    -webkit-box-align: center;
    align-items: center;
    border: 0px;
    border-radius: 16px !important;
    box-shadow: rgb(14 14 44 / 40%) 0px -1px 0px 0px inset;
    cursor: pointer;
    display: inline-flex;
    font-family: inherit;
    font-size: 16px;
    font-weight: 600;
    -webkit-box-pack: center;
    justify-content: center;
    letter-spacing: 0.03em;
    line-height: 1;
    opacity: 1;
    outline: 0px;
    transition: background-color 0.2s ease 0s, opacity 0.2s ease 0s;
    height: 32px;
    padding: 0px 16px !important;
    background-color: #1263f1 !important;
    color: white !important;
    font-weight: bold !important;
}

header.css-hip9hq-MuiPaper-root-MuiAppBar-root{
    background-color: #0e0e30;
    box-shadow: 0px 2px 4px -1px rgb(0 0 0 / 20%), 0px 4px 5px 0px #0e0e30, 0px 1px 10px 0px rgb(0 0 0 / 12%);
}

div.logo{
    color: white;
    font-weight: bold;
    font-size: 28px;
    cursor: pointer;
    background: linear-gradient(to right, #0014ff , white 53px, #f73e60 95px);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
