@import '../../sass/variables.scss';
.swap_currency_input{
    background-color: rgb(250, 249, 250);
    border-radius: 20px;
    .title{
        padding: 16px;
        padding-bottom: 0px;
        
    }
    .main{
        padding: 0.75rem 0.75rem 0.75rem 1rem;
        display: flex;
        input{
            border: 0px;
            font-size: 20px;
            flex: 1 1 auto;
            color: $pink_font_color2;
            padding: 10px;
            font-weight: bold;
        }
        input:active{
            border: 0px;
            
        }
        .token_btns{
            display: flex;
        }
        button{
            border: 0px;
            background-color: inherit;
            margin-left: 10px;
            padding: 8px;
            border-radius: 8px;
            cursor: pointer;
            img{
                margin-right: 8px;
            }
            div {
                color: rgb(114, 0, 34);
                font-size: 18px;
                font-weight: 400;
                line-height: 1.5;
                font-weight: bold;
            }
        }

        button:hover{
            background-color: #c4c4da;
            opacity: 0.8;
        }

        button:active{
            background-color: #9595b8;
            opacity: 0.8;
        }
    }
}


// mobile
@media (max-width: 740px) {
    .main{
        flex-flow: wrap;
        input{
            height: 43px;
            width: 100%;
        }
        button{
            width: 100%;
            text-align: center;
            div{
                justify-content: center;
            }
        }
    }
}

.token_modal {
    border: 1px solid rgb(231, 227, 235);
    border-radius: 32px;
    width: 480px;
    
}

// mobile
@media (max-width: 500px) {
    .token_modal{
        width: 96%;
    }
}

.modal_header{
    padding-bottom: 24px;
    border-bottom: 1px solid rgb(231, 227, 235);
    h2{
        font-weight: bold;
        font-family: Kanit, sans-serif;
    }
}

.modal_body {
    padding-top: 24px;
    input{
        background-color: rgb(238, 234, 244);
        border-radius: 16px;
        box-shadow: rgb(74 74 104 / 10%) 0px 2px 2px -1px inset;
        color: #0a0a25;
        display: block;
        font-size: 16px;
        height: 48px;
        outline: 0px;
        padding: 0px 16px;
        width: 100%;
        border: 1px solid rgb(215, 202, 236);
    }
    .token_view{
        height: 390px;
        width: 100%;
        margin-top: 16px;
        overflow: auto;
    }
}
::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-thumb {
    background: $pink_theme_btn;
    border-radius: 8px;
}
::-webkit-scrollbar-track {
    box-shadow: rgb(214, 189, 250) 0px 0px 5px inset;
    border-radius: 10px;
}
.token_item{
    display: grid;
    grid-template-columns: auto minmax(auto, 1fr) minmax(0px, 72px);
    height: 56px;
    -webkit-box-align: center;
    align-items: center;
    font-family: Kanit, sans-serif;
    border-radius: 30px;
    cursor: pointer;
    .token_img{
        img{
            width: 26px;
            height: 26px;
        }
        margin-right: 16px;
        margin-left: 16px;
    }
    span{
        font-size: 20px;
        font-family: Kanit, sans-serif;
    }
}

.token_item:hover{
    background-color: #f0efef;
}