@import '../../sass/variables.scss';
.main{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    // color: $font-color;
    font-family: Kanit, sans-serif;
}

.title{
    text-align: center;
    margin-top: 16px;
    color: $pink_theme_title;
}

.form{
    margin-top: 32px;
    width: 480px;
    display: flex;
    flex-direction: column;
    padding: 24px;
    background-color: $pink_theme_dialog_bg;
    border-radius: 24px;
}

.form > * + *{margin-top: 16px !important;}


.sliderContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.slider{color:$pink_theme_main !important}
.label{}
.button{background-color: $pink_theme_btn !important;}

