@import '../../sass/variables.scss';
.main{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: $font-color;
    font-family: Kanit, sans-serif;
}

.title{
    text-align: center;
    margin-top: 16px;
    color: $pink_theme_title;
}

.form{
    margin-top: 32px;
    width: 480px;
    display: flex;
    flex-direction: column;
    padding: 16px;
    background-color: $pink_theme_dialog_bg;
    color: black;
    border-radius: 24px;
}

.form > * + *{margin-top: 16px !important;}


.sliderContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.slider{color:$CTA !important}
.label{}
.button{background-color: $CTA !important;}

.form_header{
    display: flex;
    padding: 18px;
    border-bottom: 1px solid rgb(233, 234, 235);
}

.subtitle {
    flex: 1 1 0%;
}

.subtitle_name{
    font-size: 20px;
    font-weight: 600;
    line-height: 1.1;
}

.subtitle_desc{
    color: $pink_font_color2;
    font-weight: 400;
    line-height: 1.5;
    font-size: 14px;
    margin-top: 10px;
}

.form_btns button{
    width: 48px;
    height: 48px;
    border: 0px;
    background-color: white;
    border-radius: 16px;
}

.form_btns button:hover{
    width: 48px;
    height: 48px;
    border: 0px;
    background-color: #d9a4f7;
}

.form_btns button:active{
    width: 48px;
    height: 48px;
    border: 0px;
    background-color: #a36dfa;
}

.form_btns:hover{
    color: #a36dfa;
}

.form_body {
    // display: grid;
    grid-auto-rows: auto;
    row-gap: 12px;
}

.trans_btn{
    margin: auto;
    margin-top: 16px;
    margin-bottom: 16px;
    text-align: center;
    
    button{
        cursor: pointer;
        border-radius: 16px;
        border: 0px;
        width: 40px;
        height: 40px;
        svg{
            margin-top: 5px;
        }
    }
    button:hover{
        opacity: 0.7;
    }
    button:active{
        opacity: 0.9;
    }
}

.form_footer {
    button{
        width: 100%;
        height: 56px;
        border-radius: 16px;
        border: 0px;
        box-shadow: rgb(14 14 44 / 40%) 0px -1px 0px 0px inset;
        cursor: pointer;
        background-color: $pink_theme_btn;
        font-size: 24px;
        color: white;
        font-weight: 600;
        
    }

    button:hover{
        opacity: 0.8;
    }

    button:active{
        opacity: 0.9;
    }
}










// mobile
@media (max-width: 740px) {
    .form{
        width: 96%;
    }
}